.levelDiv {
  margin-top: 20px;
  margin-bottom: 20px;
  width: 97%;
  height: 42px;
  margin-left: 1.5%;
  font-size: 18px;
  border: 1px solid lightgray;
  box-sizing: border-box;
  justify-content: flex-start;
}

.levelItemCustom {
  width: 100vw;
  /* border: 2px solid blue; */
  padding-top: 10px;
  color: black;
}

.level {
  display: flex;
  border-radius: 5px;
  /* border: 2px solid red; */
}

.is-scrollable {
  overflow: auto;
  position: absolute;
  top: 120px;
  bottom: 70px;
  width: auto;
  -webkit-overflow-scrolling: touch;
}

.is-positive {
  background-color: #c7f6c7;
  font-weight: bold;
}

.positiveLabel {
  background-color: #c7f6c7;
  font-weight: bold;
  width: 40px;
  box-sizing: border-box;
}

.is-notSure {
  background-color: palegoldenrod;
  box-sizing: border-box;
  width: 40px;
  font-weight: bold;
}

.is-negative {
  background-color: lightpink;
  box-sizing: border-box;
  font-weight: bold;
  width: 40px;
  /* border: 2px solid red; */
}

.isNegativeStatement {
  background-color: rgba(255, 182, 193, 0.2);
}

.isUncertainStatement {
  background-color: rgba(211, 211, 211, 0.2);
}

.isPositiveStatement {
  background-color: rgba(199, 246, 199, 0.2);
}

.notSet {
  background-color: whitesmoke;
  font-weight: bold;
}

.listDiv {
  background-color: whitesmoke;
}

.levelDiv input {
  position: absolute;
  top: 1000;
  right: -1000;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;
}

.levelDiv input[type="radio"]:checked + label {
  border: 3px solid black;
}

.sortContainer {
  display: flex;
  flex-direction: column;
  margin: 5px;
  background: rgba(0, 0, 0, 0.1);
  min-height: 400px;
  max-height: 800px;
  overflow: auto;
}

.rootDiv {
  background-color: #6a9bc3;
  padding: 0px, 0px, 10px, 10px;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  -webkit-user-select: none;
  -webkit-touch-callout: none;
}

.headersContainer {
  display: flex;
  position: fixed;
  justify-content: center;
  align-items: flex-start;
  height: 50px;
  flex-direction: "row-reverse";
}

.greenSortCard {
  background: lightgreen;
  border: 2px solid gray;
}

.pinkSortCard {
  background-color: rgba(255, 182, 193, 0.4);
  border: 2px solid gray;
}

.yellowSortCard {
  background-color: lightgray;
  border: 2px solid gray;
}

/* 
.isPositiveStatement {
  background-color: rgba(199, 246, 199, 0.6);
} */

.sortFooter {
  display: flex;
  flex-direction: row;
  background: #e4e4e4;
  position: fixed;
  left: 0px;
  bottom: 0;
  width: 100%;
  height: 150px;
}

.footerMessageBox {
  flex-basis: 100px;
  padding-right: 5px;
  padding-left: 5px;
  width: 100px;
  height: 100px;
  border: 2px solid lightgray;
  text-align: center;
}

.footerMessageBox p {
  padding: 0px;
  margin-top: 10px;
  font-size: 10px;
  color: #3273dc;
}

.sortEndButton {
  position: fixed;
  bottom: 0;
  right: 0;
  margin: 10px;
}

.cardSlider {
  width: 1024px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.titleDiv {
  display: flex;
  padding-top: 20px;
  height: 350px;
  background: steelblue;
  top: 0;
  left: 0;
  position: absolute;
  overflow: hidden;
  right: 0;
}

.titleDiv h1 {
  color: white;
  font-size: 36px;
  margin-left: 30px;
}

.titleDiv h3 {
  color: white;
  width: 80%;
  height: 50px;
  font-size: 22px;
  margin-left: 30px;
}

.scrollableDiv {
  overflow: auto;
  position: absolute;
  top: 200px;
  bottom: 70px;
  left: 0px;
  right: 0px;
  background: #dedede; /* #5d80b2; */
}

.postSortFooter {
  position: absolute;
  overflow: hidden;
  background: steelblue;
  left: 0;
  bottom: 0;
}

.postSortButton {
  height: 50px;
  width: 100px;
  margin-top: 25px;
  margin-right: 15px;
  margin-left: 15px;
  font-size: 20px;
  text-align: center;
  padding-right: 5px;
}

.commentTextArea {
  background-color: whitesmoke;
  min-height: 120px;
  width: calc(100% - 10px);
}

.tagContainerDiv {
  padding-top: 3px;
  width: 100%;
}

