:root {
  --brand-1: hsl(188, 100%, 64%);
  --brand-2: hsl(212, 100%, 64%);
}

.SubmitData {
  margin: 0;
  padding: 0;

  background: whitesmoke;
}

.titleDivSubmitData {
  background: steelblue;
  height: 100px;
  padding: 20px;
}

.loadingScreenTitle {
  margin-right: 20px;
  font-size: 25px;
  font-style: italic;
  padding-top: 45px;
}

h1 {
  font-size: 36px;
  color: white;
}

.loading-screen {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: calc(100vh - 100px);
}

.bars-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.bar {
  width: 8px;
  height: 64px;

  margin: 2px;

  background: hsl(212, 100%, 64%);

  background: var(--brand-2);
  border-radius: 2px;

  -webkit-transform: scaleY(0.1);

          transform: scaleY(0.1);

  -webkit-animation: loading 1.2s ease-in-out alternate infinite;

          animation: loading 1.2s ease-in-out alternate infinite;
  -webkit-transform-origin: bottom center;
          transform-origin: bottom center;
}

.bar-1 {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
}

.bar-2 {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

.bar-3 {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}

.bar-4 {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}

.bar-5 {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}

.bar-6 {
  -webkit-animation-delay: 0.6s;
          animation-delay: 0.6s;
}

.bar-7 {
  -webkit-animation-delay: 0.7s;
          animation-delay: 0.7s;
}

.bar-8 {
  -webkit-animation-delay: 0.8s;
          animation-delay: 0.8s;
}

@-webkit-keyframes loading {
  0% {
    background: hsl(212, 100%, 64%);
    background: var(--brand-2);
    -webkit-transform: scaleY(0.1);
            transform: scaleY(0.1);
  }

  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    background: hsl(188, 100%, 64%);
    background: var(--brand-1);
  }
}

@keyframes loading {
  0% {
    background: hsl(212, 100%, 64%);
    background: var(--brand-2);
    -webkit-transform: scaleY(0.1);
            transform: scaleY(0.1);
  }

  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    background: hsl(188, 100%, 64%);
    background: var(--brand-1);
  }
}

